'use client';

import React, { useState } from 'react';
import { Button } from './ui/Button';
import { Calendar, Users, MessageCircle, User, Mail, Phone } from 'lucide-react';

interface BookingFormProps {
    serviceName: string;
    price?: number | string;
    onSubmit?: (data: any) => void;
}

export const BookingForm = ({ serviceName, price }: BookingFormProps) => {
    const [formData, setFormData] = useState({
        date: '',
        guests: 2,
        name: '',
        email: '',
        phone: '',
        message: ''
    });

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleGuestsChange = (increment: number) => {
        setFormData(prev => ({
            ...prev,
            guests: Math.max(1, prev.guests + increment)
        }));
    };

    const handleWhatsAppSubmit = (e: React.FormEvent) => {
        e.preventDefault();

        const text = `Hello, I would like to book "${serviceName}".
        
Details:
üìÖ Date: ${formData.date}
üë• Guests: ${formData.guests}
${formData.message ? `üìù Special Request: ${formData.message}` : ''}

Please confirm availability. Thank you!`;

        const encodedText = encodeURIComponent(text);
        // Replace with actual phone number from config
        const phoneNumber = '212600000000';
        window.open(`https://wa.me/${phoneNumber}?text=${encodedText}`, '_blank');
    };

    return (
        <div className="bg-white rounded-2xl shadow-xl p-6 md:p-8 border border-neutral-100 sticky top-24">
            <div className="mb-6">
                <h3 className="text-2xl font-bold font-poppins text-neutral-dark mb-1">Book Your Spot</h3>
                <p className="text-sm text-gray-500">Instant confirmation via WhatsApp</p>
            </div>

            <form onSubmit={handleWhatsAppSubmit} className="space-y-4">
                {/* Date Picker */}
                <div className="space-y-1">
                    <label className="text-xs font-bold uppercase text-neutral-dark tracking-wide flex items-center gap-2">
                        <Calendar className="w-4 h-4 text-primary" /> Select Date
                    </label>
                    <input
                        type="date"
                        name="date"
                        required
                        className="w-full bg-neutral-50 border border-neutral-200 rounded-lg px-4 py-3 outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary transition-all font-medium text-neutral-dark"
                        onChange={handleChange}
                    />
                </div>

                {/* Guests Counter */}
                <div className="space-y-1">
                    <label className="text-xs font-bold uppercase text-neutral-dark tracking-wide flex items-center gap-2">
                        <Users className="w-4 h-4 text-primary" /> Travelers
                    </label>
                    <div className="flex items-center justify-between bg-neutral-50 border border-neutral-200 rounded-lg p-2">
                        <button
                            type="button"
                            onClick={() => handleGuestsChange(-1)}
                            className="w-10 h-10 rounded-md bg-white shadow-sm flex items-center justify-center text-neutral-dark hover:bg-neutral-200 transition-colors font-bold text-xl"
                        >
                            -
                        </button>
                        <span className="font-bold text-lg text-neutral-dark">{formData.guests}</span>
                        <button
                            type="button"
                            onClick={() => handleGuestsChange(1)}
                            className="w-10 h-10 rounded-md bg-white shadow-sm flex items-center justify-center text-primary hover:bg-primary/10 transition-colors font-bold text-xl"
                        >
                            +
                        </button>
                    </div>
                </div>



                {/* Submit Button */}
                <button
                    type="submit"
                    className="w-full bg-[#25D366] hover:bg-[#20b858] text-white font-bold py-4 rounded-xl shadow-lg hover:shadow-xl transition-all flex items-center justify-center gap-2 mt-4 group"
                >
                    <MessageCircle className="w-6 h-6 fill-current" />
                    <span>Book via WhatsApp</span>
                </button>

                <p className="text-xs text-center text-gray-400 mt-2">
                    No payment required now. You pay locally.
                </p>
            </form>
        </div>
    );
};
